<template>
  <iframe
    frameborder="0"
    scrolling="no"
    :class="`facebookIframe ${this.isMobile && 'isMobile'}`"
    :src="url"
  />
</template>

<script>
export default {
  props: {
    isMobile: {
      default: true,
      type: Boolean,
    },
  },
  computed: {
    url() {
      return !this.isMobile ? "./fbPosts.html" : "./fbPostsMobile.html";
    },
  },
};
</script>

<style lang="scss" scoped>
.facebookIframe {
  width: 400;
  height: 500px;
  margin: 0px;
  padding: 0px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  margin-bottom: 5rem;
}
</style>